	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Overload","date":"19 Mar 2001","slug":"/2001-03-19-overload/","tags":"Y2001"},"html":"<p><b>Late</b><br />\nSo, here I am performing a major update of my employer's website from the comfort of my own home. I'd normally feel more comfortable doing this after midnight rather than at 9pm, but the current sleep schedule dictates otherwise.</p>\n<p>This is the 4th web agency I've worked with, and the first that leaves a competent impression. They don't have a clue about security, though, but neither did the others.</p>\n<p><b>Middle</b><br />\nIt's snowing here, again. I thought I left Minnesota to avoid this kind of weather!</p>\n<p>North of Hamburg they're having a real blizzard, with force 11 winds (they still use the Beaufort scale here... that's around 70 mph), with lots of closed roads and schools.</p>"},"next":{"fields":{"title":"Breaking Things","date":"17 Mar 2001","slug":"/2001-03-17-breaking-things/"}},"previous":{"fields":{"title":"Madman Situation","date":"20 Mar 2001","slug":"/2001-03-20-madman-situation/"}}},{"node":{"fields":{"title":"Breaking Things","date":"17 Mar 2001","slug":"/2001-03-17-breaking-things/","tags":"Y2001"},"html":"<p><b>Noon</b><br />\nChristopher has a fat lip. He slipped from the couch and landed with his lip on the corner of Mama's old doll house. There was some blood at first as two teeth cut into his lip, but he's eating and drinking just fine. He just looks like a battered boxer.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/03/crhboxer.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"crhboxer.jpg: \" /></p>\n<p><b>Early</b><br />\nSomething is very wrong with my internal body clock I used to to think that 5 am was a time of day that existed only in limbo. Now, even though I can sleep until 5:45 on work days, I'm always awake before 5:00. Even on weekends, when I can sleep in.</p>"},"next":{"fields":{"title":"Dot Bomb","date":"15 Mar 2001","slug":"/2001-03-15-dot-bomb/"}},"previous":{"fields":{"title":"Overload","date":"19 Mar 2001","slug":"/2001-03-19-overload/"}}},{"node":{"fields":{"title":"Dot Bomb","date":"15 Mar 2001","slug":"/2001-03-15-dot-bomb/","tags":"Y2001"},"html":"<p>Or dead com. Or dot zombie. I don't know which of these we are, but Mama now reminds me daily that she achieves our yearly worldwide sales every 3 weeks or so in northern Germany alone. Now she gets to make executive decisions like who gets the new office chairs, and what to do when when too many PCs are ordered.</p>\n<p>And like a bad penny, CeBIT stress has reared it's ugly head again. Why take the time to do something right, when something, <i>anything</i> has to be ready by March 22? But don't tell me about until a week beforehand. </p>\n<p>At day care Christopher refuses to drink the fruit tea we send along, holding out for apple juice. But at home he drinks the fruit tea. I guess he's already practicng manipulation of adults.</p>"},"next":{"fields":{"title":"Sweet dreams are made of this","date":"14 Mar 2001","slug":"/2001-03-14-sweet-dreams-are-made-of-this/"}},"previous":{"fields":{"title":"Breaking Things","date":"17 Mar 2001","slug":"/2001-03-17-breaking-things/"}}}],"pathPrefix":"","first":false,"last":false,"index":1014,"pageCount":1101}}

